import React,{useState,useEffect} from 'react'
import {db} from "../firebase-config"
import { collection , getDocs, startAt} from "firebase/firestore"
import { query, orderBy,where, limit,serverTimestamp } from "firebase/firestore";  
import {updateDoc,doc} from "firebase/firestore"
import "./home.css"
import { async } from '@firebase/util';

export default function Home() {



    var webdata="";
    const [webdatac, setwebdatac] = useState("")
    const [count,setcount]=useState("");
     const [ids, setids] = useState(null)
    var id=null;
    const [isch, setisch] = useState(true)

    const getdata = async()=>{
        function generateRandom(maxLimit = 100){
            let rand = Math.random() * maxLimit; 
            rand = Math.floor(rand); // 99
            return rand;
        }
        setisch(true);
        setwebdatac(null);
        const userCollectionRef = collection(db,"count")
        const countd = await getDocs(userCollectionRef);
        setcount(countd.docs.map((doc)=>({...doc.data(),id:doc.id})))
        let index = generateRandom(count&&count[0].count);
        console.log(index);
        const data = await getDocs(query(collection(db, "data"),limit(1),where("id","==",`${index}`)));
        webdata=data&&data.docs.map((doc)=>({...doc.data(),id:doc.id}))
        setwebdatac(webdata);
        console.log(webdata);
        id=webdata&&webdata[0].id;
        console.log(id)
        setids(id);

    };
    const addfirst=async()=>{
        const userDoc = doc(db,"data",ids);
        const newFields = {first_count:Number(webdatac[0].first_count)+1};
        setwebdatac([{first_count:Number(webdatac[0].first_count)+1,
                    scond_count:webdatac[0].scond_count,
                    first_choose:webdatac[0].first_choose,
                    scond_choose:webdatac[0].scond_choose
                    }]);
        show();
        await updateDoc(userDoc,newFields)
    }

    const addscond=async()=>{
        const userDoc = doc(db,"data",ids);
        const newFields = {scond_count:Number(webdatac[0].scond_count)+1};
        setwebdatac([{first_count:webdatac[0].first_count,
            scond_count:Number(webdatac[0].scond_count)+1,
            first_choose:webdatac[0].first_choose,
            scond_choose:webdatac[0].scond_choose
            }]);
        show();
        await updateDoc(userDoc,newFields)
    }

    const show=()=>
    {
        setisch(false);
    }


    if(webdatac == "")
    {
    return (
        <>
            <div className="whead">لو خيروك</div>
            <button onClick={getdata} className="startbutton" >اضغط للبداء</button>
        </>
    )
    }
    else if(webdatac == null)
    {
        return (
            <>
            <div className="whead">لو خيروك</div>
            <div className="loading">Loading...</div>
            </>
        )
    }
    else
    {
    return (
        <>
            <div className="whead">لو خيروك</div>
            <div className="cont">
                {isch?<div className="left box" onClick={addfirst}>{webdatac[0].first_choose}</div>:<div className="left box" style={{"width":`${Math.round((Number(webdatac[0].first_count)/(Number(webdatac[0].first_count)+Number(webdatac[0].scond_count)))*100)}%`}} >{Math.round((Number(webdatac[0].first_count)/(Number(webdatac[0].first_count)+Number(webdatac[0].scond_count)))*100)}%</div>}
                {isch?<div className="right box" onClick={addscond}>{webdatac[0].scond_choose}</div>:<div className="right box" style={{"width":`${Math.round((Number(webdatac[0].scond_count)/(Number(webdatac[0].first_count)+Number(webdatac[0].scond_count)))*100)}%`}} >{Math.round((Number(webdatac[0].scond_count)/(Number(webdatac[0].first_count)+Number(webdatac[0].scond_count)))*100)}%</div>}
            </div>
            <button onClick={getdata} className="nextbutton" >التالي</button>
        </>
    )
    }

}

