import React,{useState,useRef} from 'react'
import {collection, getDocs,addDoc} from "firebase/firestore";
import {updateDoc,doc} from "firebase/firestore"
import {db} from "../firebase-config"
import Swal from 'sweetalert2'
import "./add.css"


export default function Add() {
  const [name1, setname1] = useState("");
  const [name2, setname2] = useState("");
  var count=0;
  const name1input = useRef(null)
  const name2input = useRef(null)
  const createhandle = async() => {
    if(name1input.current.value.trim() != ""&&name2input.current.value.trim() != ""){
    const userCollectionRef = collection(db,"count")
    const countd = await getDocs(userCollectionRef);
    count = countd.docs.map((doc)=>({...doc.data(),id:doc.id}))
    const userCollectionRefadd = collection(db,"data")

    await addDoc(userCollectionRefadd,{first_choose:name1,scond_choose:name2,id:`${count[0].count}`,first_count:1,scond_count:1});
    const userDoc = doc(db,"count","count");
    const newFields = {count:count[0].count+1};
    await updateDoc(userDoc,newFields)
    name1input.current.value="";
    name2input.current.value="";
    setname1("");
    setname2("");
    Swal.fire({
      title: 'Done',
      icon: 'success',
      confirmButtonText: 'okey'
    })
  }
  else
  {
    Swal.fire({
      title: 'the input is empty',
      icon: 'error',
      confirmButtonText: 'okey'
    })
  }
  };

  return (
    <>
      <div className="whead">add</div>
      <input className="winput" type="text" ref={name1input} placeholder="name1" onChange={(e) => {setname1(e.target.value);}}/>
      <input className="winput" type="text" ref={name2input} placeholder="name2" onChange={(e) => {setname2(e.target.value);}}/>
      <button className="wbutton" onClick={createhandle}>create</button>
    </>
  )
}
